.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 0 1 auto;
            flex: 0 1 auto;
    padding: 15px;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
            clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden
}

input {
    box-sizing: border-box;
    padding: 5px;
    margin: 5px 5px 0 0;
    font-size: 14px;
    width: 100%;
    max-width: 80%;
}

button {
    box-sizing: border-box;
    margin: 5px 5px 0 0;
    padding: 5px;
    font-size: 14px;
}

.error-text {
    margin: 5px 0;
    color: #cf0202;
}

/*points*/
.point-panel {
    -webkit-flex: 50% 1;
            flex: 50% 1;
    max-width: 50%;
    padding: 0 50px 0 0;
}

.point-panel__form {
    margin: 0 0 15px;
}

.point {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 0 1 auto;
            flex: 0 1 auto;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 10px;
    background: #f7f7f7;
    border-radius: 5px;
    margin: 10px 0;
}

.point__delete {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    display: inline-block;
    vertical-align: middle;
    width: 16px;
    height: 16px;
    margin-left: 10px;
    position: relative;
    z-index: 5;
    cursor: pointer;
    background: url(/map-routes/static/media/remove.628b9f74.svg) 50% 50% no-repeat;
    opacity: 0.65;
    background-size: contain;
    padding: 0 3px;
}

.point__delete:hover {
    opacity: 1;
}

/*route-map*/
.ya-route {
    -webkit-flex: 50% 1;
            flex: 50% 1;
    max-width: 50%;
}

.ya-route__map {
    max-width: 100%;
}

@media(max-width: 600px) {
    input, button {
        width: 100%;
        max-width: 100%;
        margin-right: 0;
    }

    .point-panel {
        padding: 0 20px 0 0;
    }
}

